import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { PostService } from '/users/preya/desktop/angular/angrouting/src/app/services/post.service';

@Component({
  selector: 'app-postdetail',
  templateUrl: './postdetail.component.html',
  styleUrls: ['./postdetail.component.css']
})
export class PostdetailComponent implements OnInit {
  id:any;
  data :any;
  constructor(ar:ActivatedRoute,myapi:PostService, private router: Router) {
    this.id = ar.snapshot.params['id'];
    myapi.getDataById(this.id).subscribe((s :any)  => this.data = s)
   }

  ngOnInit(): void {
  }

  onBack(){
    this.router.navigate(['/post']);
  }

}
