import { Component, OnInit } from '@angular/core';
import { Observable, Observer, of } from 'rxjs';

@Component({
  selector: 'app-observable',
  template: `
    <h1> Observable </h1>
    <button style = "border:3px solid black; background-color: lightgreen; font-size : 15px " (click) = "call()"> Subscribe </button>
    <div stlye = " font-size : 15px"> Time is {{time | async}} </div>
    <div stlye = " font-size : 15px"> {{source | async}} </div>
  `,
  styles: [
  ]
})
export class ObservableComponent implements OnInit {
  arr:number[] = [1,2,3,4,5];
  source:Observable<number[]>;
  constructor() {
    this.source = of(this.arr);
    
  }
  call(){
    this.source.subscribe(a => console.log(a),e=>console.log('error'),()=>console.log('Complete'));
    this.time .subscribe(a => console.log(a),e=>console.log('error'),()=>console.log('Complete'));
    
  }

  ngOnInit(): void {
  }

  time = new Observable<string> ((observer : Observer<string>) =>{
    setInterval(() => observer.next(new Date().toString()),1000);
  });
}


